body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
  display: -webkit-flex;
  display: flex; 
  -webkit-justify-content: space-between; 
          justify-content: space-between;
}

header .a > h3{
  margin-left: 1em;
  font-size: 3em;
}

header .b {
  margin-right: 1em;
  font-size: 3em;
}

.note-button-saved{
  
}
.container{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

button > .a{
    font-weight: bolder;
}

.note-sample-display{
    padding-left: 1em;
    height: 20em;

    border: .2em solid rgba(0, 0, 0, .2);
    width: 72%;
    font-size: 1.5em;
    height: 8em;
}
.page-container{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-top: 3%;
    width: 78%;
    margin: auto;
}

.page-container .input-field {
    width: 100%;
}

.page-container .input-field .a {
    font-size: 3em;
    width: 100%;
}

.page-container .save {
    width: 100%;
    height: 4em;
}


